import { api } from "./config";

export default {
  headers: {
    method: "get",
    headers: {
      Accept: "application/json",
      "Content-Type": "application/json"
    }
  },
  async get(url) {
    try {
      if (this.headers.body !== undefined) delete this.headers.body;
      this.headers.method = "get";
      const req = await fetch(api + url, this.headers);
      if (!req.ok) return { error: true, message: req.statusText };
      const res = await req.json();
      return res;
    } catch (message) {
      return { error: true, message };
    }
  },
  async post(url, params = {}) {
    try {
      this.headers.body = JSON.stringify(params);
      this.headers.method = "post";
      const req = await fetch(api + url, this.headers);
      if (!req.ok) return { error: true, message: req.statusText };
      const res = await req.json();
      return res;
    } catch (message) {
      return { error: true, message };
    }
  },
  async delete(url) {
    try {
      if (this.headers.body !== undefined) delete this.headers.body;
      this.headers.method = "delete";
      const req = await fetch(api + url, this.headers);
      if (!req.ok) return { error: true, message: req.statusText };
      const res = await req.json();
      return res;
    } catch (message) {
      return { error: true, message };
    }
  }
};
