import React from "react";
import Loader from "../components/Loader";
import Navbar from "../components/Navbar";
import request from "../helpers/request";
import {
  Icon,
  Grid,
  TextField,
  MenuItem,
  Button,
  ExpansionPanel,
  ExpansionPanelDetails,
  ExpansionPanelSummary,
  TableRow,
  TableCell,
  Table,
  TableBody,
  TableHead
} from "@material-ui/core";

class Reportes extends React.Component {
  state = {
    projects: [],
    loading: true,
    projectId: "",
    sDate: "",
    eDate: "",
    data: {
      rows: {},
      keys: []
    }
  };
  componentDidMount() {
    this.GetProjects();
  }

  async GetProjects() {
    try {
      const response = await request.get("/lista");
      if (response.error) {
        console.log(response.message);
        alert("Favor de revisar la consola.");
        this.setState({ loading: false });
      } else {
        this.setState({ projects: response.data, loading: false });
      }
    } catch (message) {
      console.log(message);
      alert("Favor de revisar la consola.");
    }
  }

  fetchOC = async () => {
    try {
      this.setState({ loading: true });
      const { projectId, sDate, eDate, projects } = this.state;
      if (projectId === "") return alert("Debe de seleccionar un proyecto");
      if (sDate === "") return alert("Debe de seleccionar la fecha inicial");
      if (eDate === "") return alert("Debe de seleccionar la fecha final");
      let url = "/estado-cuentas?";
      url += "segmento=" + projects[projectId].CCODIGOPROYECTO + "&";
      url += "proyecto=" + projects[projectId].CNOMBREPROYECTO + "&";
      url += "fechaInicial=" + sDate + "&";
      url += "fechaFinal=" + eDate;
      const response = await request.get(url);
      this.setState({ loading: false });
      if (response.error) return alert(response.message);

      let a = document.createElement("a");
      a.href = "/api/proyectos/reporte/" + response.data;
      a.setAttribute(
        "download",
        "Reporte de cuentas  - " + projects[projectId].CNOMBREPROYECTO
      );
      a.click();
      a = null;
    } catch (error) {
      alert("Ocurrio un error, favor de revisar en la consola");
      console.log(error);
    }
  };

  selectProject = ev => {
    this.setState({ projectId: ev.target.value });
  };
  handleChange = ev => {
    const { value, name } = ev.target;
    this.setState({ [name]: value });
  };
  render() {
    const { projects, loading, projectId, data } = this.state;
    return (
      <>
        <Loader show={loading} />
        <Navbar
          options={[{ view: "Reportes", label: "Reportes" }]}
          onClick={view => this.props.navigation.go(view)}
        />
        <Grid container spacing={4}>
          <Grid item md={4}>
            <TextField
              id="standard-select-currency"
              select
              fullWidth
              label="Selecciona un Proyecto"
              value={projectId}
              onChange={this.selectProject}
              helperText="Por favor selecciona una opcion"
            >
              {projects.map((project, i) => (
                <MenuItem key={project.CIDPROYECTO} value={`${i}`}>
                  {project.CCODIGOPROYECTO + "  - " + project.CNOMBREPROYECTO}
                </MenuItem>
              ))}
            </TextField>
          </Grid>
          <Grid item md={3}>
            <TextField
              fullWidth
              type="date"
              name="sDate"
              onChange={this.handleChange}
              label="Fecha Inicial"
              InputLabelProps={{
                shrink: true
              }}
            />
          </Grid>
          <Grid item md={3}>
            <TextField
              fullWidth
              type="date"
              name="eDate"
              onChange={this.handleChange}
              label="Fecha Final"
              InputLabelProps={{
                shrink: true
              }}
            />
          </Grid>
          <Grid item md={2}>
            <Button variant="contained" color="primary" onClick={this.fetchOC}>
              Buscar
            </Button>
          </Grid>
        </Grid>
        <div className="mt-2">
          {data.keys.map(key => (
            <ExpansionPanel key={key}>
              <ExpansionPanelSummary
                expandIcon={<Icon>expanded_more_icon</Icon>}
              >
                {key}
              </ExpansionPanelSummary>
              <ExpansionPanelDetails>
                <Table>
                  <TableHead>
                    <TableRow>
                      <TableCell>O.C.</TableCell>
                      <TableCell>Fecha</TableCell>
                      <TableCell>Descripci√≥n</TableCell>
                      <TableCell>Detalle</TableCell>
                      <TableCell>Cantidad</TableCell>
                      <TableCell>Precio</TableCell>
                      <TableCell>Total</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {data.rows[key].map(row => (
                      <TableRow key={row.CIDDOCUMENTO}>
                        <TableCell>
                          {row.CSERIEDOCUMENTO + " - " + row.CFOLIO}
                        </TableCell>
                        <TableCell>{row.CFECHA}</TableCell>
                        <TableCell>{row.CNOMBREPRODUCTO}</TableCell>
                        <TableCell>{row.COBSERVAMOV}</TableCell>
                        <TableCell>{row.CUNIDADESCAPTURADAS}</TableCell>
                        <TableCell>{row.CPRECIOCAPTURADO}</TableCell>
                        <TableCell>{row.CNETO}</TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </ExpansionPanelDetails>
            </ExpansionPanel>
          ))}
        </div>
      </>
    );
  }
}

export default Reportes;
